import React from 'react';
import {render} from 'react-dom';


var A=React.createClass({
    _dragStart:function(event){

        event.dataTransfer.setData("A",'it is component A');
    },
    render:function(){

        return (
            <div style={{border:"1px dashed #00f"}} className="A" draggable={true} onDragStart={this._dragStart} ref="A">
                it is component A
            </div>
        );
    }
});
module.exports=A;


