import React from 'react';
import {render} from 'react-dom';
var B=React.createClass({
    drop:function(ev){
        var data =ev.dataTransfer.getData('A');
        this.setState({drop: data});
    },
    dragOver:function(ev){
        ev.preventDefault();
        console.log('...it is over it');
    },
    dragEnter:function(){
        console.log('...it is dragEnter');
    },
    dragLeave:function(){
        console.log('....it is dragLeave');
    },
    prefixDragEnter:function(){
        console.log('...prefix drag enter');
    },
    prefixDragLeave:function(){
        console.log('...prefix drag leave');
    },
    getInitialState:function(){
        return ({drop: null});
    },
    render:function(){

        var child=null;
        if(this.state.drop!==undefined&&this.state.drop!==null)
            child=this.state.drop;

        return (
            <div>
                <div className="prefix" onDragEnter={this.prefixDragEnter()} onDragLeave={this.prefixDragLeave}></div>
                <div style={{border:"1px dashed #00f",height:"500px",width:"300px"}} className="B"
                     onDrop={this.drop}  onDragOver={this.dragOver}
                     onDragEnter={this.dragEnter}
                     onDragLeave={this.dragLeave}
                     ref="B">
                    <span>it is component B</span>

                    <div>
                        {child}
                    </div>
                </div>
                <div className="suffix"></div>
            </div>

        );
    }
});
module.exports=B;


